@charset "UTF-8";
@import 'variables';

/*  Unstyled list */

ul.unstyled-list {
    list-style: none;
    padding-left: 0em;
    margin-bottom: 0;
}

ul.unstyled-list>li {
    list-style: none;
    float: left;
    position: relative;
}

ul.unstyled-list>li a {
    text-decoration: none;
    display: inline-block;
    vertical-align: middle;
}

/* Info List - col-4 metadata */

ul.info-list i.material-icons {
    color: $black;
    line-height: 25px;
    font-size: 18px;
    vertical-align: middle;
}

ul.info-list>li {
    display: inline-block;
    position: relative;
    word-break: break-all;
    vertical-align: top;
}

ul.info-list>li>a {
    font-size: $font-size-h4;
    line-height: 25px;
    display: block;
    cursor: default;
}

ul.info-list>li:first-child {
    width: 40%;
}

ul.info-list>li:first-child>a {
    color: $dark-grey;
}

ul.info-list>li>a.active {
    color: $green;
}


/* Box list */

ul.box-tools-list {
    float: left;
    margin: 0;
}

ul.box-tools-list>li {
    width: 1.875em;
    display: inline-block;
    vertical-align: middle;
    text-align: left;
    position: relative;
}


/* Filter list */

ul.filter-list {
    float: left;
    margin-left: 2.8125em;
}

ul.filter-list>li {
    display: inline-block;
    vertical-align: middle;
    float: left;
    position: relative;
}

li.title>a {
    font-size: $font-size-h4;
    font-weight: bold !important;
    color: $black !important;
    margin-left: 10px;
}

li.title:first-child>a {
    // margin-left: 0px;
}

ul.box-tools-list li.title {
    width: 112px;
}


/* Nav list */

ul.nav-list {
    float: left;
    margin-bottom: 0;
}

ul.nav-list>li {
    display: inline-block;
    padding: 8px 13px;
    position: relative;
    vertical-align: middle;
}

ul.nav-list>li>a {
    color: $white;
    display: block;
    text-align: center;
    text-decoration: none;
}

ul.nav-list .material-icons {
    position: relative;
    display: inline-block;
}

ul.nav-list>li ul.dropdown-menu>li>a>i.material-icons {
    vertical-align: middle;
}

ul.nav-list>li ul.dropdown-menu>li>a>i.material-icons>.badge-red {
    right: 0;
}

ul.nav-list>li ul.dropdown-menu>li>a>span {
    display: inline-block;
    vertical-align: text-top;
}