@import 'variables';

.dashboard-wrapper {
    position: relative;
}

.dashboard-left-block {
    width: 450px;
    display: block;
    position: absolute;
    z-index: 999999;
    left: 0;
}

.dashboard-right-block {
    width: 450px;
    display: block;
    position: absolute;
    z-index: 999999;
    right: 0;
}

.media.active {
    background: $dark-blue;
    color: #fff;
    position: relative;
}

.dashboard-right-block .media-heading{
    // position: relative;
    font-size: 18px;
    width: 100%;
    line-height: 24px;
    margin-bottom: 12px;
    font-weight: bold;
}

.dashboard-right-block .media-heading:after{
    content: '';
    position: absolute;
    height: 1px;
    background: #fff;
    width: 82%;
    top: 39px;
    right: 0;
    left: 0;
    margin-left: auto;
    margin-right: auto;
}

.media-body p{
    font-size:12px;
}

.dashboard-left-block h6{
    color:$dark-grey;
}

.dashboard-right-block p{
    color:$dark-grey;
}

.dashboard-right-block .media.active p{
    color:#fff;
}

.dashboard-right-block .media.active .media-heading{
    font-size:24px;
    font-weight: normal;
}

/* Main settings */


