        background-color: $light-grey-tb;
    }

    .btn-search{
        height: 28px;
        line-height: 12px;
        width: calc(100% - 55px);
        text-align: left;
        border-radius: 0;
        padding-left: 55px;
    }

}

.modal-header {
    border-bottom: 0.0625em solid #f5f5f5;
