@charset "UTF-8";
@import 'variables';

/* Spin laoder */

#circle-loader-wrap {
    overflow: hidden;
    position: relative;
    margin-top: -10px;
    width: 24px;
    height: 24px;
    margin: 2px 10px;
    box-shadow: none;
    background-color: transparent;
    border-radius: 200px;
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}

#circle-loader-wrap:after {
    content: '';
    position: absolute;
    left: 15px;
    top: 15px;
    width: 170px;
    border-radius: 50%;
    background-color: #EEDAC2;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

#circle-loader-wrap div {
    overflow: hidden;
    position: absolute;
    width: 50%;
    height: 100%;
}

#circle-loader-wrap .loader {
    position: absolute;
    left: 100%;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #004E70;
}

#circle-loader-wrap .left-wrap {
    left: 0;
}

#circle-loader-wrap .left-wrap .loader {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    transform-origin: 0 50% 0;
    -webkit-transform-origin: 0 50% 0;
    animation: loading-left 2s infinite linear;
    -webkit-animation: loading-left 2s infinite linear;
    
}

#circle-loader-wrap .right-wrap {
    left: 50%;
}

#circle-loader-wrap .right-wrap .loader {
    left: -100%;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    transform-origin: 100% 50% 0;
    -webkit-transform-origin: 100% 50% 0;
    animation: loading-right 2s infinite linear;
    -webkit-animation: loading-right 2s infinite linear;
}

@keyframes loading-left {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(180deg);
    }
    50% {
        transform: rotate(180deg);
    }
    75% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(180deg);
    }
}

@-webkit-keyframes loading-left {
    0% {
        -webkit-transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotate(180deg);
    }
    50% {
        -webkit-transform: rotate(180deg);
    }
    75% {
        -webkit-transform: rotate(180deg);
    }
    100% {
        -webkit-transform: rotate(180deg);
    }
}

@keyframes loading-right {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(180deg);
    }
    75% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(180deg);
    }
}

@-webkit-keyframes loading-right {
    0% {
        -webkit-transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotate(0deg);
    }
    50% {
        -webkit-transform: rotate(180deg);
    }
    75% {
        -webkit-transform: rotate(180deg);
    }
    100% {
        -webkit-transform: rotate(180deg);
    }
}


.circle-center {
    position: absolute !important;
    width: 18px !important;
    height: 18px !important;
    // background: #004e70;
    z-index: 999999;
    border: 3px solid #f5f5f5;
    top: 3px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
}


/* Loader */

.cssload-container {
    width: 100%;
    position: absolute;
    margin-top: 50px;
    height: 100%;
    background: #efefef;
    padding: 20%;
    text-align: center;
    z-index: 9999;
}

.cssload-speeding-wheel {
    width: 49px;
    height: 49px;
    margin: 0 auto;
    border: 3px solid rgb(0, 0, 0);
    border-radius: 50%;
    border-left-color: transparent;
    border-right-color: transparent;
    animation: cssload-spin 575ms infinite linear;
    -o-animation: cssload-spin 575ms infinite linear;
    -ms-animation: cssload-spin 575ms infinite linear;
    -webkit-animation: cssload-spin 575ms infinite linear;
    -moz-animation: cssload-spin 575ms infinite linear;
}

@keyframes cssload-spin {
    100% {
        transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-o-keyframes cssload-spin {
    100% {
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-ms-keyframes cssload-spin {
    100% {
        -ms-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes cssload-spin {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-moz-keyframes cssload-spin {
    100% {
        -moz-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.spinner1 {
    width: 20px;
    height: 20px;
    margin: 0 auto;
    border: 2px solid;
    border-radius: 50%;
    border-color: transparent rgb(0, 78, 112) rgb(0, 78, 112);
    -webkit-animation: spin 600ms infinite linear;
    animation: spin 600ms infinite linear
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}


.spinner-loader{

    overflow: hidden;
    position: relative;
    width: 40px;
    height: 24px;
    margin: 4px auto;
    box-shadow: none;

    .progress {
        width: 22px;
        height: 22px;
        line-height: 22px;
        background: none;
        margin: 0 auto;
        margin: 0 auto;
        box-shadow: none;
        position: relative;
    }
    
    .progress:after {
        content: "";
        width: 100%;
        height: 100%;
        border-radius: 50%;
        border: 2px solid #fff;
        position: absolute;
        top: 0;
        left: 0;
    }
    
    .progress > span {
        width: 50%;
        height: 100%;
        overflow: hidden;
        position: absolute;
        top: 0;
        z-index: 1;
    }
    
    .progress .progress-left {
        left: 0;
    }
    
    .progress .progress-bar {
        width: 100%;
        height: 100%;
        background: none;
        border-width: 2px;
        border-style: solid;
        position: absolute;
        top: 0;
    }
    
    .progress .progress-left .progress-bar {
        left: 100%;
        border-top-right-radius: 80px;
        border-bottom-right-radius: 80px;
        border-left: 0;
        -webkit-transform-origin: center left;
        transform-origin: center left;
    }
    
    .progress .progress-right {
        right: 0;
    }
    
    .progress .progress-right .progress-bar {
        left: -100%;
        border-top-left-radius: 80px;
        border-bottom-left-radius: 80px;
        border-right: 0;
        -webkit-transform-origin: center right;
        transform-origin: center right;
        animation: loading-1 1.8s linear forwards;

    }
    
    .progress .progress-value {
        width: 85%;
        height: 85%;
        border-radius: 50%;
        border: 2px solid #ebebeb;
        font-size: 32px;
        line-height: 125px;
        text-align: center;
        position: absolute;
        top: 7.5%;
        left: 7.5%;
    }
    
    .progress.blue .progress-bar {
        border-color: #004E70;
    }
    
    .progress.blue .progress-value {
        color: #004E70;
    }
    
    .progress.blue .progress-left .progress-bar {
        animation: loading-2 1.5s linear forwards 1.8s;
    }

    
    .progress.yellow .progress-bar {
        border-color: #fdba04;
    }
    
    .progress.yellow .progress-value {
        color: #fdba04;
    }
    
    .progress.yellow .progress-left .progress-bar {
        animation: loading-3 1s linear forwards 1.8s;
    }
    
    .progress.pink .progress-bar {
        border-color: #ed687c;
    }
    
    .progress.pink .progress-value {
        color: #ed687c;
    }
    
    .progress.pink .progress-left .progress-bar {
        animation: loading-4 0.4s linear forwards 1.8s;
    }
    
    .progress.green .progress-bar {
        border-color: #1abc9c;
    }
    
    .progress.green .progress-value {
        color: #1abc9c;
    }
    
    .progress.green .progress-left .progress-bar {
        animation: loading-5 1.2s linear forwards 1.8s;
    }

    .second{
        position: absolute;
        top: 5px;
        left: 0;
        right: 0;
        width: 12px;
        height: 12px;
    }

    .second .progress-bar{
      /*  background: #004E70;*/
    }
}


@keyframes loading-1 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
    }
}

@keyframes loading-2 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
    }
}

@keyframes loading-3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(90deg);
        transform: rotate(90deg);
    }
}

@keyframes loading-4 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(36deg);
        transform: rotate(36deg);
    }
}

@keyframes loading-5 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(126deg);
        transform: rotate(126deg);
    }
}

@media only screen and (max-width: 990px) {
    .progress {
        margin-bottom: 20px;
    }
}