@charset "UTF-8";
@import 'variables';
@import 'fonts';
@font-face {
    font-family: 'Helvetica Neue';
    src: url(../fonts/HelveticaNeue.eot);
    src: url(../fonts/HelveticaNeue.eot?#iefix) format("embedded-opentype"), url(../fonts/HelveticaNeue.woff) format("woff"), url(../fonts/HelveticaNeue.ttf) format("truetype");
    font-weight: 400;
    font-style: normal;
}

body {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    font-size: $font-size-default;
    background: $bg-grey;
}

body.overflow-hidden {
    overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: 500;
    color: inherit;
}

h1,
.h1 {
    font-size: $font-size-h1
}

h2,
.h2 {
    font-size: $font-size-h2
}

h3,
.h3 {
    font-size: $font-size-h3
}

h4,
.h4 {
    font-size: $font-size-h4
}

h5,
.h5 {
    font-size: $font-size-h5
}

h6,
.h6 {
    font-size: $font-size-h6
}

h3,
.h3 {
    font-size: 18px;
}

.semibold{
    font-weight: 600;
}

.mobile-view {
    display: none;
}

.no-apdding {
    padding: 0 !important;
}

.bg-transparent {
    background: transparent !important;
}

.browser-resize {
    display: none;
    padding: 28vh 1em;
}

.iconxl {
    font-size: 24vw !important;
    color: $dark-blue;
}

.container-fluid {
    max-width: $container-max-width;
}

.visible-hidden {
    visibility: hidden;
}

 :focus {
    outline: none !important;
}

a {
    color: $black;
}

.no-border {
    border: 0 !important;
    border: none !important;
}

.no-margin {
    margin: 0 !important;
}

.no-padding {
    padding: 0 !important;
}

.mr-r0 {
    margin-right: 0px !important;
}

.mr-l0 {
    margin-left: 0px !important;
}



.nav-main-menu ul.nav-list>li>a>span {
    margin-top: -5px;
}

.panelbox-gutterpadding {
    padding: 0 0.9375em 0 0.9375em !important;
}

.panelbox-gutterpadding-first-child {
    padding: 0.625em 0.9375em 0 0.9375em !important;
}

.panelbox-gutterpadding-last-child {
    padding: 0 0.9375em 0.625em 0.9375em !important;
}

.bagde-notf {
    position: absolute;
    height: 6px;
    width: 6px;
    top: 0px;
    right: -7px;
    border-radius: 50%;
}

.badge-red {
    @extend .bagde-notf;
    background: $red;
}

.badge-blue {
    @extend .bagde-notf;
    background: $dark-blue;
}

.nav-tabs {
    border-bottom: none;
}

header.header {
    background: $dark-blue;
    max-height: 3.25em;
    height: 3.25em;
    color: $white;
}

.well.well-unstyled {
    min-height: 1.25em;
    padding: 0;
    margin-bottom: 0;
    background-color: transparent;
    border: 0;
    border-radius: 0;
    box-shadow: none;
    padding-left: 1.25em;
}

.main-header-div {
    width: 100%;
    display: block;
    float: left;
    height: 3.25em;
    position: relative;
}



ul.main-tools {
    float: left;
}

ul.ui-tabs {
    margin-top: 0px;
    float: left;
}




.more .material-icons {
    display: inline-block;
    vertical-align: middle;
}

.more .material-icons>span {
    display: inline-block;
    vertical-align: middle;
}



header .right-header-menu ul.nav-list>li:last-child {
    padding-right: 0px;
}

ul.nav-list>li>a>span {
    display: block;
    font-size: 0.5625em;
    text-transform: capitalize;
}

.user-account-block,
.right-menu-block {
    display: block;
}

.user-account-block {
    float: left;
    display: inline-block;
    width: 10.875em;
    margin-top: 0.5em;
    margin-left: 0.3125em;
}

.media.user-account-media h6 {
    font-size: $font-size-h5;
    color: $dark-grey;
    margin-top: 4px;
}

.user-account-block .media-body {
    vertical-align: middle;
}

.media-heading {
    font-size: 0.75em;
    margin-bottom: 0em;
}

.right-menu-block {
    float: right;
}

.main-menu-block {
    display: inline-block;
    margin-left: 10px;
}

img.img-logo-white {
    width: 5.25em;
    padding-top: 9px;
}


/* sub header */

.subheader {
    width: 100%;
    height: 4.25em;
    display: block;
    float: left;
    padding: 1.3125em 0em;
}

span.date {
    display: block;
    font-size: 0.53125em;
}

.account-logo {
    float: left;
    width: 15em;
    display: block;
}

/* sub header button styles on _buttons.scss */

.icon-spin {
    width: 40px;
    text-align: center;
    line-height: 30px;
    vertical-align: middle;
}

/******************* Main block *******************/

.right-menu .box-panel .box-panel-body {
    min-height: 80px;
}

.box-panel {
    width: 100%;
    display: block;
    float: left;
    background: $white;
    border-radius: 0.25em;
    margin-bottom: 0.4em;
    .box-panel-header,
    .box-panel-body,
    .box-panel-footer {
        float: left;
        width: 100%;
        display: block;
        padding: 6px 15px;
        position: relative;
    }
    .box-panel-header {
        border-bottom: 0.0625em solid $bg-grey; // height: 1.875em;
        height: 40px;
        z-index: 1;
        -webkit-user-select: none;
        -moz-user-select: none;
        -khtml-user-select: none;
        -ms-user-select: none;
    }
    .box-panel-body {
        // min-height: 30em;
        .box-panel-body-header {
            width: 100%;
            display: block;
            margin-bottom: 10px;
        }
        .box-panel-body-header::after {
            content: '';
            width: 100%;
            height: 1px;
            position: absolute;
            top: 42px;
            left: 0;
            border-bottom: 1px solid $bg-grey;
        }
        .box-panel-body-header::before {
            content: '';
            width: 100%;
            height: 1px;
            position: absolute;
            top: 0px;
            left: 0;
            border-top: 1px solid $bg-grey;
        }
    }
}


.jq-left-menu-db,
.jq-right-menu-db,
.jq-tablebar-db {
    // cursor: pointer;
}


/* widgets */

.widgets {
    margin-top: 0.75em;
}

.media.user-account-media {
    padding: 0.9375em;
    color: $black;
    position: relative;
    width: 100%;
}

.metadata-content {
    padding: 0.6em 0;
}

.metadata-content ul li a {
    cursor: default;
}

.user-account-dropdown-menu {
    margin: 0;
    padding: 0;
    border-radius: 0.5em;
    position: absolute;
    background: #fff;
    border: 0.0625em solid #cccccc;
    border: 0.0625em solid rgba(0, 0, 0, 0.15);
    border-radius: 0.25em;
    box-shadow: 0 0.375em 0.75em rgba(0, 0, 0, 0.175);
    background-clip: padding-box;
    z-index: 999999;
    top: 3.25em;
    display: none;
    max-width: 27em;
}

.user-account-dropdown-menu .media-body {
    width: 70%;
}

.media.user-account-media.footer .media-body {
    width: 100%;
}

.user-account-dropdown-menu>li.account-list {
    border: 0.0625em solid $bg-grey;
}

.user-account-dropdown-menu .user-account-dropdown-menu {
    box-shadow: none;
    border-radius: 0;
    position: relative;
    top: 0;
    border: 0;
}

.media-tools {
    display: table-cell;
    vertical-align: middle;
    width: 312.5em;
}

.media-tools>ul.box-tools-list>li {
    width: 25px;
}

.collpase-div {
    display: none;
}

.show-on-hover:hover {
    ul.dropdown-menu {
        display: block;
    }
}

.div-2>.box-panel>.box-panel-header .box-panel-header {
    padding: 0 15px;
    height: 0px;
    border: 0;
}

.div-2>.box-panel>.box-panel-header {
    display: table;
    padding-right: 0;
    padding-left: 0;
}

.box-panel-icon-lefmenushow,
.box-panel-icon-rightmenushow,
.box-panel-icon-tablebarshow {
    display: table-cell;
    width: 1.875em;
    vertical-align: middle;
    text-align: center;
    position: relative;
}

.box-panel-icon-lefmenushow {
    padding-right: 15px;
    padding-left: 15px;
}

.box-panel-icon-rightmenushow {
    padding-left: 15px;
    padding-right: 15px;
}

.box-panel-icon-tablebarshow {
    padding-left: 15px;
    padding-right: 15px;
}

.box-panel-icon-lefmenushow::after {
    content: '';
    border-left: 2px solid #f5f5f5;
    position: absolute;
    right: 0;
    top: -10px;
    height: 43px;
}

.box-panel-icon-tablebarshow::after {
    content: '';
    border-left: 2px solid #f5f5f5;
    position: absolute;
    right: 0;
    top: -10px;
    height: 43px;
}

.box-panel-icon-rightmenushow::before {
    content: '';
    border-left: 2px solid #f5f5f5;
    position: absolute;
    left: 0;
    top: -10px;
    height: 43px;
}

.disabled .material-icons {
    color: $dark-grey;
}

.tooltip>.tooltip-inner {
    background-color: $dark-blue;
}

.more .bagde-notf {
    top: 3px;
    left: -7px;
    right: auto;
}

.box-panel .box-panel-body.tab-pane {
    display: none;
}

.box-panel .box-panel-body.tab-pane.active {
    display: block;
}

ul.ui-tabs>li {
    float: left;
    position: relative;
}

ul.ui-tabs>li>a {
    font-size: $font-size-h4;
    font-weight: normal;
    color: $dark-grey;
    margin-left: 10px;
    display: inline-block;
    vertical-align: middle;
}

.ou-list {
    .media.active {
        background: #004e70;
        color: #fff;
        margin: -10px -15px 0 -15px;
        margin-top: -10px;
        padding: 0.625em 0.9375em;
    }
}

.mn-settings {
    img.media-object {
        width: 75px;
    }
    ul.info-list>li:first-child {
        width: 45%;
    }
    .media-heading {
        font-size: 18px;
        margin-bottom: 6px;
    }
}

p.text-filed {
    margin-left: 40px;
    color: #999;
    font-size: 12px;
    margin-bottom: 16px;
}

.settings-form {
    margin-top: 0px;
    display: block;
    float: left;
    width: 100%;
    ul.info-list>li:first-child {
        margin-right: 20px;
        text-align: right;
    }
}

.svg-icon {
    img {
        width: 22px;
        height: 22px;
        position: absolute;
        height: 22px;
        left: 0;
        top: 8px;
        right: 0;
        margin: 0 auto;
        transition: all .3s ease-in;
    }
    span {
        margin-top: 26px !important;
    }
}

.color-grey {
    color: $dark-grey;
}



.media.user-details {
    margin-top: 10px;
    margin-bottom: 20px;
    margin-left: 40px;
    margin-right: 0;
    .media-left {
        padding-right: 20px;
    }
    p.text-filed {
        margin-left: 40px;
        color: #999;
        font-size: 12px;
    }
    .media-body p {
        font-size: 12px;
        color: #999;
    }
}



