@charset "UTF-8";
@import 'variables';

.material-icons {
    font-size: 22px;
    cursor: pointer;
    vertical-align: middle;
}

.material-icons.icon-30 {
    width: 30px;
    font-size: 22px;
    text-align: center;
    display: inline-block;
    float: left;
}

.material-icons.icon-48 {
    width: 48px;
    height: 48px;
    font-size: 22px;
    text-align: center;
    display: inline-block;
    float: left;
    padding: 6px;
}

.icon-hover>.svgf {
    // display: none;
    width: 22px;
    opacity: 0;
}

.icon-hover:hover>.svgf {
    // display: inline;
    opacity: 1;
}

.icon-hover:hover>.svgo {
    opacity: 0;
}

.icon-green {
    color: $green;
}

.icon-blue {
    color: $dark-blue
}

.icon-grey {
    color: $light-grey-ac;
}

.icon-disabled {
    color: $dark-grey;
}

.icon-search {
    position: absolute;
    left: 20px;
    top: 4px;
    color: $dark-blue;
}

.icon-12 {
    font-size: 12px;
}

img.user-icon-48 {
    width: 48px;
}

img.user-icon-45 {
    width: 45px;
}

img.user-icon-75 {
    width: 75px;
}