@charset "UTF-8";
@import 'variables';

/* Toggle switch */

.switch {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 14px;
}

.switch input {
    display: none;
}

.slider-toggle {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f40;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider-toggle:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: -4px;
    bottom: -3px;
    border: 1px solid #eaeaea;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked+.slider-toggle {
    background-color: #07de16;
}

input:focus+.slider-toggle {
    box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider-toggle:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}


/* Rounded sliders */

.slider-toggle.round {
    border-radius: 34px;
}

.slider-toggle.round:before {
    border-radius: 50%;
}