@charset "UTF-8";
@import 'variables';

.btn-primary {
    color: #ffffff;
    background-color: $dark-blue;
    border-color: $dark-blue;
}

.btn-hvr {
    display: none;
}

.btn-hover {
    float: left;
    display: inline-block;
    position: absolute;
    top: 11px;
    padding-right: 25px;
    width: 100%;
    padding-left: 10px;
}

.btn-red {
    background: red;
    color: #fff;
}

.btn-orange {
    background: orange;
    color: #fff;
}

.btn-hover:hover .btn-hvr {
    display: block;
    float: right;
    width: 24px;
    padding: 2px;
    height: 24px;
    margin: 0 3px;
    border-radius: 50%;
}

.btn.disabled,
.btn[disabled],
fieldset[disabled] .btn {
    cursor: not-allowed;
    opacity: 1;
    -webkit-box-shadow: none;
    background: #f5f5f5;
    box-shadow: none;
    pointer-events: none;
}


.btm-sm {
    height: 28px;
    line-height: 12px;
}

.btn-circle {
    width: 18px;
    height: 18px;
    text-align: center;
    padding: 6px 0;
    font-size: 8.5px;
    line-height: 6px;
    border-radius: 50%;
}


.btn-default:hover{
    color: #333333;
    background-color: #ffffff;
    border-color: #fff;
}

.btn-default:active:hover, .btn-default.active:hover, .open > .dropdown-toggle.btn-default:hover, .btn-default:active:focus, .btn-default.active:focus, .open > .dropdown-toggle.btn-default:focus, .btn-default:active.focus, .btn-default.active.focus, .open > .dropdown-toggle.btn-default.focus {
    color: #333333;
    background-color: #ffffff;
    border-color: #fff;
    outline: none;
    box-shadow: none;
}

.btn-default:focus, .btn-default.focus {
    color: #333333;
    background-color: #ffffff;
    border-color: #fff;
    outline: none;
}

.btn-primary:active, .btn-primary.active, .open > .dropdown-toggle.btn-primary {
    background: #004e70;
    color: #fff;
    border-color: #004e70;
    outline: none;
}

.btn-primary:active:hover, .btn-primary.active:hover, .open > .dropdown-toggle.btn-primary:hover, .btn-primary:active:focus, .btn-primary.active:focus, .open > .dropdown-toggle.btn-primary:focus, .btn-primary:active.focus, .btn-primary.active.focus, .open > .dropdown-toggle.btn-primary.focus {
    background: #004e70;
    color: #fff;
    border-color: #004e70;
}

.btn:active, .btn.active {
    outline: 0;
    background-image: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}

/* Subheader buttons styles */

.subheader-btn-group .btn {
    height: 29px;
    border-radius: 8px;
    border-color: transparent;
    padding: 0px 24px;
    vertical-align: middle;
    margin: 0 10px;
    text-align: center;
    line-height: 29px;
    font-size: $font-size-h4;
}

.subheader-btn-group .btn-default:active,
.subheader-btn-group .btn-default.active {
    color: $black;
    background-color: $white;
    box-shadow: none;
    border-color: none;
    box-shadow: none;
    font-weight: bold;
    border-width: 0;
}

.btn-search {
    width: 14em;
    padding-left: 45px;
    color: #333333;
    background-color: white;
    height: 29px;
    border-radius: 8px;
    border-color: transparent;
    padding: 0px 12px;
    vertical-align: middle;
    margin: 0 10px;
    text-align: left;
    line-height: 28px;
    box-shadow: none;
}

.btn-search i.material-icons {
    margin-top: 4px;
}

.subheader-btn-group .btn-search {
    width: 14em;
    padding-left: 45px;
    margin-right: 0;
    font-size: $font-size-h4;
}