@charset "UTF-8";
@import 'variables';

ul.nav-treeview > li .ion-icon {
  font-size: 22px;
  vertical-align: middle;
  margin-left: -17px;
  margin-right: 0;
}

.nav-unstyled {
  list-style: none;
  padding-left: 0;
}

.nav-treeview li.active > a{
  color: #fff;
  background: #085374;
  margin: 0 -400px !important;
  padding: 5px 410px !important;
  width: auto !important;
}

.nav-treeview li a{
  padding: 5px 10px;
  display: inline-block;
  vertical-align: middle;
  font-size: 12px;
  text-decoration: none;
}

.left-menu{
  .box-panel-body{
      overflow: hidden;
  }
}

.nav-treeview .material-icons{
  font-size:18px;
  margin-left: -11px;
  margin-right: 3px;
}
.nav-treeview li{
  // margin:0 -15px;
  // padding: 0 40px;
}

.nav-treeview ul{
  padding-left:25px;
}

.nav-treeview li.has-subtree ul{
  opacity: 0;
  height: 0;
  visibility: hidden;
  transition: opacity .1s ease-in-out;
}

.nav-treeview li.has-subtree.open > ul{
  opacity: 1;
  height: auto;
  visibility: visible;
}
