@charset "UTF-8";
@import 'variables';

.dropdown-menu>li>a {
    padding: 3px 15px;
}

.dropdown-menu {
    padding: 7px 0;
    min-width: 150px;
    box-shadow: none;
    border-width: 1px;
    z-index: 9999999;
}

.dropdown-menu>li>a {
    font-size: $font-size-h4;
    width: 100%;
}

.dropdown-menu-right {
    left: auto !important;
    right: 0 !important;
}

.box-tools-list .dropdown-menu {
    margin-top: 7px;
}

ul.dropdown-ar>li>a {
    color: $dark-grey;
    display: inline-block;
    padding-top: 2px;
    vertical-align: middle;
}

.dropdown-menu>li>a:hover,
.dropdown-menu>li>a:focus {
    background-color: $hover-blue;
    color: $black;
}