@charset "UTF-8";
@import 'variables';

.form-design{
    box-shadow: none;
    height: 28px;
    padding: 5px 10px;
    font-size: 12px;
}

.form-group{
    float: left;
    width: 100%;
}

.form-horizontal .control-label {
    text-align: left;
}

.form-default .form-control{
    @extend .form-design;
}
.form-default .control-label{
    font-size: $font-size-sl;
}

.form-modern .form-control{
    @extend .form-design;
    border: 0;
    border-radius: 0;
    border-bottom: 1px solid #cccccc;
}
.form-modern .control-label{
    font-size: $font-size-sl;
}

.form-material .form-group{
    padding-top:15px;
    // font-family: 'Roboto', sans-serif;
}
.form-material .form-control{
    @extend .form-design;
    border: 0;
    border-radius: 0;
    border-bottom: 1px solid #cccccc;
}
.form-material .control-label{
    font-size: $font-size-sl;
    position: absolute;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-align: left;
    transition : all .3s  cubic-bezier(0.4, 0, 0.2, 1);
    top:0;
}

.form-material .control-label::after{
    background-color: #3f51b5;
    content: '';
    height: 2px;
    bottom: -27px;
    margin-left: auto;
    margin-right: auto;
    top: 27px;
    left: 0;
    right: 0;
    position: absolute;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
    -webkit-transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    width: 10px;
    visibility: hidden;
}


.is-focused .control-label::after{
    visibility: visible;
    width: calc(100%);
    left: 0;
    bottom: -27px;
    top: 43px;
}

.is-focused .control-label{
    font-size: 8px;
    top: -16px;
    overflow: visible;
    color:#3f51b5;
}

input:-webkit-autofill {
    // Much nicer
    background-color: #371E01;
}

.form-group input.form-control.error{
    border-bottom: 1px solid #FF0000 !important;
}
.validation-error{
    color: red;
    font-size: 12px;
}

.btn-showpassword{
    position: absolute;
    right: 7px;
    top: -3px;
    box-shadow: none;
}

.form-control.password{
    padding-right: 54px;
}