@charset "UTF-8";
@import 'variables';



@font-face {
	font-family: "m-icon";
	src: url('./m-icon.eot');
	src: url('./m-icon.eot?#iefix') format('eot'),
		url('./m-icon.woff2') format('woff2'),
		url('./m-icon.woff') format('woff'),
		url('./m-icon.ttf') format('truetype'),
		url('./m-icon.svg#m-icon') format('svg');
}
.icon{
    position: relative;
    /* font-size: 43px; */
    font-family: "m-icon";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    text-decoration: none;
    text-transform: none;
	font-size: 26px;
	width:30px;
    font-weight: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -webkit-font-feature-settings: 'liga';
    -webkit-font-smoothing: antialiased;
}
.icon:before {
	font-family: "m-icon";
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	font-style: normal;
	font-variant: normal;
	font-weight: normal;
	/* speak: none; only necessary if not using the private unicode range (firstGlyph option) */

}

